
.slider {
  position: relative;
  min-height: 1px;
  overflow: hidden;
}
.slider .slider-group {
  position: relative;
  overflow: hidden;
  white-space: nowrap;
}
.slider .slider-group .slider-item {
  float: left;
  box-sizing: border-box;
  overflow: hidden;
  text-align: center;
}
.slider .slider-group .slider-item a {
  display: block;
  width: 100%;
  overflow: hidden;
  text-decoration: none;
}
.slider .slider-group .slider-item img {
  display: block;
  width: 100%;
}
.slider .dots {
  position: absolute;
  right: 0;
  left: 0;
  bottom: 12px;
  text-align: center;
  font-size: 0;
}
.slider .dots .dot {
  display: inline-block;
  margin: 0 4px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #838484;
  opacity: 0.5;
}
.slider .dots .dot.active {
  opacity: 0.9;
  background: #dc2828;
}


