
.progress-bar {
  margin: 0 auto;
  width: 90%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.progress-bar .current-date,
.progress-bar .total-date {
  -webkit-box-flex: 40px;
      -ms-flex: 40px 0 0px;
          flex: 40px 0 0;
  color: #fff;
  text-align: center;
  font-size: 14px;
}
.progress-bar .bar-wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  height: 2px;
  background: rgba(255,255,255,0.5);
}
.progress-bar .bar-wrapper .progress {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 40%;
  height: 2px;
  background: #dc2828;
}
.progress-bar .bar-wrapper .progress-dot {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  -webkit-transform: translate3d(50%, -50%, 0);
          transform: translate3d(50%, -50%, 0);
  display: block;
  height: 15px;
  width: 15px;
  border-radius: 50%;
  background: #f0f0f0;
  z-index: 11;
}


