
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  background: #161719;
  font-size: 14px;
  line-height: 35px;
  box-shadow: -1px 2px 3px #111214;
}
.tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  color: #838484;
}
.tab .tab-item .tab-link {
  padding-bottom: 2px;
}
.tab .tab-item.router-link-active {
  color: #dc2828;
  border-bottom: 2px solid #dc2828;
}


