
.subnav {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #161719;
}
.subnav .item {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  height: 100px;
}
.subnav .item .item-link {
  width: 100%;
  line-height: 80px;
  font-size: 25px;
  color: #838484;
}
.subnav .item .item-link:before {
  position: relative;
  padding: 12px;
  border: 2px solid #838484;
  border-radius: 50%;
}
.subnav .item .text {
  position: relative;
  top: -5px;
  font-size: 12px;
}
.subnav .item .today {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  font-size: 12px;
  text-align: center;
  line-height: 88px;
}


