
.list {
  position: relative;
  width: 100%;
}
.list .title-bar {
  padding: 10px 0;
}
.list .title-bar .title {
  padding-left: 8px;
  font-weight: 500;
  font-size: 14px;
  border-left: 2px solid #dc2828;
}
.list .content {
  position: relative;
}
.list .content .item-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.list .content .item-list .item {
  margin: 5px 0;
}
.list .content .item-list .item.item-1 {
  width: 100%;
}
.list .content .item-list .item.item-2 {
  width: 50%;
}
.list .content .item-list .item.item-3 {
  width: 33.33%;
}
.list .content .item-list .item .img {
  width: 100%;
}
.list .content .item-list .item .text {
  margin: 5px;
  font-size: 12px;
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  content: '';
}


