
.slid-enter-active,
.slid-leave-active {
  transition: all 0.5s;
}
.slid-enter,
slid-leave {
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.side-menu {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  width: 80%;
  height: 100%;
  overflow: hidden;
  background: #161719;
}


