
.headers {
  width: 100%;
  background: #161719;
}
.headers .nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 50px;
  color: #838484;
  text-align: center;
}
.headers .nav .side {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 60px;
          flex: 0 0 60px;
}
.headers .nav .center {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.headers .nav .center .item {
  padding: 0 20px;
}
.headers .side-menu-wrapper {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: rgba(2,4,6,0.8);
}


