
.foot-list {
  position: fixed;
  bottom: 0;
  height: 300px;
  width: 100%;
  z-index: 120;
  background: #111214;
}
.foot-list .header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  margin: 0 15px;
}
.foot-list .header .mode {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.foot-list .header .collection {
  -webkit-box-flex: 60px;
      -ms-flex: 60px 0 0px;
          flex: 60px 0 0;
  text-align: center;
}
.foot-list .header .clear {
  -webkit-box-flex: 60px;
      -ms-flex: 60px 0 0px;
          flex: 60px 0 0;
  text-align: center;
}
.foot-list .list-wrapper {
  color: #9a9b9c;
  overflow: hidden;
  height: 100%;
}
.foot-list .list-wrapper .song {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0 15px;
  border-bottom: 1px solid #161719;
}
.foot-list .list-wrapper .song .text {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 0px;
          flex: 1 0 0;
}
.foot-list .list-wrapper .song .text .name {
  font-size: 14px;
  color: #cbcbcb;
  line-height: 35px;
}
.foot-list .list-wrapper .song .text .description {
  font-size: 10px;
  color: #838484;
  line-height: 15px;
}
.foot-list .list-wrapper .song .ctrl {
  -webkit-box-flex: 20px;
      -ms-flex: 20px 0 0px;
          flex: 20px 0 0;
  text-align: center;
  font-size: 10px;
}


